@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&family=Secular+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:300, 400, 700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

.NavbarItems{
    position: relative;
    background-color: white;
    display: flex;
    justify-content:right;
    align-items: center;
    padding: 10px 20px;
    width: 100%;
    height: 70px;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    z-index: 99;
}
.navbar-box{
    width: 100%;
    display: flex;
    justify-content: flex-end;
    flex-wrap: nowrap;
    flex-direction: row;
}
.navbar-logo{
    color: rgb(13, 131, 13);
    font-size: 1.8rem;
    font-weight: 800;
    cursor: pointer;
}
.nav-menu{
    display: grid;
    grid-template-columns: repeat(5,auto);
    grid-gap: 20px;
    list-style: none;
    margin-top: 15px ;
    width: 50%;
    align-items: center;
}
.nav-links{
    text-decoration: none;
    font-size: 1.1rem;
    color: black;
    font-weight: 400;
}
.nav-links:hover{
    color: rgb(106, 106, 241);
}
.nav-links-mobile{
    text-decoration: none;
    font-size: 1.1rem;
    color: black;
    font-weight: 400;
    display: none;
}
button{
  color: #fff;
  padding: 15px 25px;
  border-radius: 100px;
  background-color: #4C43CD;
  background-image: radial-gradient(93% 87% at 87% 89%, rgba(0, 0, 0, 0.23) 0%, transparent 86.18%), radial-gradient(66% 87% at 26% 20%, rgba(255, 255, 255, 0.41) 0%, rgba(255, 255, 255, 0) 69.79%, rgba(255, 255, 255, 0) 100%);
  box-shadow: 2px 19px 31px rgba(0, 0, 0, 0.2);
  font-weight: bold;
  font-size: 16px;

  border: 0;

  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;

  cursor: pointer;
}

.menu-icon{
    display: none;
    position: absolute;
    /* margin-left: 400px; */
   
}
@media screen and (max-width : 850px) {
    .NavbarItems{
        z-index: 99;
    }
    .menu-icon{
        display: block;
    }
    .nav-menu{
        display: flex;
        background-color:white;
        border-radius: 20px;
        box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
        flex-direction: column;
        width: 100%;
        height: auto;
        position: absolute;
        top: 90px;
        left: -100%;
        opacity: 1;
        align-items: center;
        padding-bottom: 15px ;
        margin: 0;
        z-index: -1;
    }
    .nav-menu.active{
        left: 0;
        opacity: 1;
        z-index: 1;
    }
    .nav-links{
        display: block;
        width: 100%;
        font-size: 1rem;
        padding: 1rem 0;
    }
    button{
        display: none;
    }
    .nav-links-mobile{
        display: block;
        color: #fff;
        padding: 15px 25px;
        border-radius: 100px;
        background-color: #4C43CD;
        background-image: radial-gradient(93% 87% at 87% 89%, rgba(0, 0, 0, 0.23) 0%, transparent 86.18%), radial-gradient(66% 87% at 26% 20%, rgba(255, 255, 255, 0.41) 0%, rgba(255, 255, 255, 0) 69.79%, rgba(255, 255, 255, 0) 100%);
        box-shadow: 2px 19px 31px rgba(0, 0, 0, 0.2);
        font-weight: bold;
        font-size: 16px;
      
        border: 0;
      
        user-select: none;
        -webkit-user-select: none;
        touch-action: manipulation;
      
        cursor: pointer;
    }
}

.hero{
    font-family: 'Secular One', sans-serif;
    width: 100%;
    height: 100%;
    position: relative;
}
.img{
    height: auto;
    max-width: 100%;

   
}
.hero-img img {
    height: 600px;
    width: 100%;
    object-fit: cover;
    z-index: -1;
}
.hero-text{
    font-family: 'Secular One', sans-serif;

    align-items: center;
    text-align: center;
    position: absolute;
    top: 40%;
    /* left: 50%; */
    translate: (-50%, -50%);
    width: 100%;
}
.hero-text h1{
    font-family: 'Secular One', sans-serif;

    /* margin-top: -100px; */
    font-size: 3rem;
    font-weight: 800;
    background: white;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.hero-text p{
    font-family: 'Secular One', sans-serif;

    font-size: 1.5rem;
    color: white;
    font-weight: 800;
    width: 100%;
    align-items: center;
    text-align: center;
    padding: 0.5rem 0,2rem;
}

@media screen and ( max-widht: 555px) {
    
    .hero-text{
        top: 20%;
    }
    .hero-text h1{
        
        padding: 10px 10px;
        font-size: 1.3rem;
    }
    .hero-text p{
        font-size: 1.2rem;
        padding: 0 0 2rem 0;
    }
}
footer{
	position: relative;
	bottom: 0;
}
.footer-distributed{
	background-color: #292c2f;
	box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.12);
	box-sizing: border-box;
	width: 100%;
	text-align: left;
	font: bold 16px sans-serif;

	padding: 55px 50px;
	margin-top: 80px;
}

.footer-distributed .footer-left,
.footer-distributed .footer-center,
.footer-distributed .footer-right{
	display: inline-block;
	vertical-align: top;
}

.footer-distributed .footer-left{
	width: 40%;
}

.footer-distributed h3{
	color:  #ffffff;
	font: normal 36px 'Cookie', cursive;
	margin: 0;
}

.footer-distributed h3 span{
	color:  #5383d3;
}


.footer-distributed .footer-links{
	
	margin: 20px 0 12px;
    display: grid;
    grid-template-columns: (6,auto);
    grid-gap: 5px;
	padding: 0;
	line-height: 1.8;
	color:  inherit;
    list-style: none;
}
.footer-links-text{
    color:  #ffffff;
    text-decoration: none;
}

.footer-distributed .footer-company-name{
	color:  #8f9296;
	font-size: 14px;
	font-weight: normal;
	margin: 0;
}


.footer-distributed .footer-center{
	width: 35%;
}

.footer-distributed .footer-center i{
	background-color:  #33383b;
	color: #ffffff;
	font-size: 25px;
	width: 38px;
	height: 38px;
	border-radius: 50%;
	text-align: center;
	line-height: 42px;
	margin: 10px 15px;
	vertical-align: middle;
}

.footer-distributed .footer-center i.fa-envelope{
	font-size: 17px;
	line-height: 38px;
}

.footer-distributed .footer-center p{
	display: inline-block;
	color: #ffffff;
	vertical-align: middle;
	margin:0;
}

.footer-distributed .footer-center p span{
	display:block;
	font-weight: normal;
	font-size:14px;
	line-height:2;
}

.footer-distributed .footer-center p a{
	color:  #5383d3;
	text-decoration: none;;
}

.footer-distributed .footer-right{
	width: 20%;
}

.footer-distributed .footer-company-about{
	line-height: 20px;
	color:  #92999f;
	font-size: 13px;
	font-weight: normal;
	margin: 0;
}

.footer-distributed .footer-company-about span{
	display: block;
	color:  #ffffff;
	font-size: 14px;
	font-weight: bold;
	margin-bottom: 20px;
}

.footer-distributed .footer-icons{
	margin-top: 25px;
}

.footer-distributed .footer-icons a{
	display: inline-block;
	width: 35px;
	height: 35px;
	cursor: pointer;
	background-color:  #33383b;
	border-radius: 2px;

	font-size: 20px;
	color: #ffffff;
	text-align: center;
	line-height: 35px;

	margin-right: 3px;
	margin-bottom: 5px;
}


@media (max-width: 880px) {

	.footer-distributed{
		font: bold 14px sans-serif;
	
	}

	.footer-distributed .footer-left,
	.footer-distributed .footer-center,
	.footer-distributed .footer-right{
		display: block;
		width: 100%;
		margin-bottom: 40px;
		text-align: center;
	}

	.footer-distributed .footer-center i{
		margin-left: 0;
	}
	.main {
		line-height: normal;
		font-size: auto;
	}

}
